<template>
  <div class="container">


    <div class="reg" v-if="toggle && !logged">
      <h1>Register</h1>
      <div class="create-user">
        <label for="name">Name:</label>
        <input type="text"  id="name" value="" placeholder="E.g. Michael Cera" v-model="name"><br>
      <br>  <label for="email">Email:</label>
        <input type="text"  id="email" value="" placeholder="example@email.com" v-model="email"><br>
      <br>  <label for="password">Password: </label>
        <input type="text"  id="password" value="" placeholder="Password" v-model="password">
        <h2>{{msg}}</h2>
        <button type="button" name="button" v-on:click="createUser()">Register</button>
      </div>
      <button type="button" v-on:click="clearFields()" name="button">Log in</button>
    </div>


    <div class="log" v-if="!toggle && !logged">
      <h1>Login</h1>
      <div class="login-user">
        <label for="lemail">Email:</label>
        <input type="text"  id="lemail" value="" placeholder="example@email.com" v-model="email"><br>
      <br>  <label for="lpassword">Password: </label>
        <input type="text"  id="lpassword" value="" placeholder="Password" v-model="password">
        <h2>{{msg}}</h2>
        <button type="button" name="button" v-on:click="accessUser()">Login</button>
      </div>
      <button type="button" v-on:click="clearFields()" name="button">Register</button>
    </div>

    <div class="us" v-if="logged">
      <h1>Users</h1>
      <button type="button" name="button" v-on:click="listUsers()">See users</button>
      <ul>
        <li v-for="user in users"  :key="user._id">Name: {{user.name}} <br>Email: {{user.email}}</li>
      </ul>
    </div>
    <hr>
    <p class="error" v-if="error">{{error}}</p>

  </div>
</template>

<script>
import Register from '../Register';
import Login from '../Login';
import Users from '../Users';

export default {
  name: 'register',
  data(){
    return{
      toggle: true,
      logged: false,
      name: '',
      email: '',
      password: '',
      msg: '',
      error: '',
      token: '',
      users:{},
    }
  },
  methods:{
    async createUser(){
      try{
        var res = await Register.createUser(this.name, this.email, this.password);
        this.msg = res.data.msg;
        this.error = '';
      }catch(err){
        this.error = err.response.data.msg;
      }
    },
    loguser(){
      console.log(this.name);
    },
    async accessUser(){
      try{
        var res = await Login.loginUser(this.email, this.password);
        this.msg = res.data.msg;
        this.token = res.data.token;
        this.logged = !this.logged;
        this.error = '';
      }catch(err){
        this.error =err.response.data.msg;
      }
    },
    clearFields(){
      this.name = '';
      this.email = '';
      this.password = '';
      this.msg = '';
      this.error = '';
      this.toggle = !this.toggle;
    },
    async listUsers(){
      try{
        var res = await Users.getUsers(this.token);
        this.users = res.data.all;
      }catch(err){
        this.error = err.response.data.msg;
      }
    }
  }
}
</script>

